<template>
  <div class="search-container">
    <!--  -->
    <!-- van-search form action  iOS -->
    <van-form aciton="/">
      <van-search
        v-model="searchText"
        placeholder=""
        background="#3296fa"
        show-action
        @cancel="$router.back()"
        @search="onSearch"
        @focus="isResultShow = false"
      ></van-search>
    </van-form>
    <!--  -->
    <!--  -->
    <search-result v-if="isResultShow" :q="searchText" />
    <!--  -->
    <!--  -->
    <search-suggestion
      v-else-if="searchText"
      :q="searchText"
      @search="onSearch"
    />
    <!--  -->
    <!--  -->
    <search-history v-model="searchHistories" v-else @search="onSearch" />
    <!--  -->
  </div>
</template>

<script>
import searchHistory from './components/search-history'
import searchResult from './components/search-result'
import searchSuggestion from './components/search-suggestion'
import { setItem, getItem } from '@/utils/storage'
export default {
  name: 'SearchIndex',
  data() {
    return {
      searchText: '', //
      isResultShow: false, // 
      searchHistories: getItem('search-histories') || [] //
    }
  },
  components: {
    searchHistory,
    searchResult,
    searchSuggestion
  },
  watch: {
    //
    searchHistories(newVal) {
      // 
      setItem('search-histories', newVal)
    }
    // <=>searchHistories : {handle () { }}
  },
  methods: {
    onSearch(q) {
      this.searchText = q
      //  
      const searchHistories = this.searchHistories
      const index = searchHistories.indexOf(q)
      if (index !== -1) {
        searchHistories.splice(index, 1)
      }
      searchHistories.unshift(this.searchText)
      this.isResultShow = true
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .van-search__action {
  color: #ffffff;
}
</style>
