<template>
  <van-cell-group>
    <van-cell title="">
      <template v-if="isDeleteShow">
        <span @click="$emit('update:search-histories', [])"></span>
        &nbsp;&nbsp;
        <span @click="isDeleteShow = false"></span>
      </template>
      <van-icon v-else name="delete" @click="isDeleteShow = true"></van-icon>
    </van-cell>
    <van-cell
      :title="item"
      v-for="(item, index) in searchHistories"
      :key="index"
      @click="onHistoryClick(item, index)"
    >
      <van-icon name="close" v-show="isDeleteShow"></van-icon>
    </van-cell>
  </van-cell-group>
</template>

<script>
export default {
  name: 'searchHistory',
  props: {
    searchHistories: {
      type: Array,
      default: () => []
    }
  },
  //  v-model  search-histories 
  // v-model: search-histories  , props 
  // model    model 
  model: {
    prop: 'search-histories',
    event: 'update:search-histories'
  },
  data() {
    return {
      isDeleteShow: false // 
    }
  },
  methods: {
    onHistoryClick(item, index) {
      if (this.isDeleteShow) {
        // 
        this.searchHistories.splice(index, 1)
      } else {
        // 
        this.$emit('search', item)
      }
    }
  }
}
</script>

